.main-navigation__menu-btn {
  width: 3rem;
  height: 3rem;
  background: transparent;
  border: none;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-right: 2rem;
  cursor: pointer;
}

.main-navigation__menu-btn span {
  display: block;
  width: 3rem;
  height: 2.5px;
  background: white;
}

.main-navigation__title {
  color: white;
}

.main-navigation__title a{
  text-decoration: none;
  color: white;
  font-weight: bold;
  letter-spacing: 5px;
  font-size: 30px;
  text-shadow: 5px 5px 10px black;
  
}

.main-navigation__header-nav {

  display: none;
}


.main-navigation__drawer-nav {
  height: 100%;
}

@media (min-width: 868px) {
  .main-navigation__menu-btn {
    display: none;
  }
 
  .cart1{
    display: none;
    margin-left:10px ;
  }

  .main-navigation__header-nav {
    display: block;
  }
}


@media (max-width: 868px) {
  
  .main-navigation__title a{
    font-size: 16px;
    letter-spacing: 1px;
  }
  .cart1{
    margin-left:50px ;
  }
  .main-navigation__menu-btn span{
    width: 24px;
  }
}

@media (max-width: 300px){
  .main-navigation__title a{
    font-size: 12px;
    letter-spacing: 0px;
  }
  .cart1{
    margin-left:10px ;
  }
}